@import '../common/variables.scss';
.slider__toggles {
  padding: 10px;
  margin: 0;
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: center;
  height: 10px;
  font-size: 0;
}

.slider__toggle {
  width: 10px;
  height: 10px;
  border: 1px solid #a5a5a5;
  background: transparent;
  transform: rotate(45deg);
  margin-right: 20px;
}

.slider__toggle.active {
  background: white;
}

.slider__toggle.active-second {
  background: #353535;
}

.slider-arrow {
  position: absolute;
  top: -1px;
  display: none;
  width: 50px;
  height: calc(100% + 2px);
  font-size: 0;
  background-color: #fff;
  background-repeat: no-repeat;
  background-position: center;
  border: 1px solid #e5ddd5;
  outline: none;
  cursor: pointer;
  transition-property: background-color, border-color;
  transition-duration: .1s;

  &:hover {
    border-color: #d5c9bd;
  }

  &:active {
    background-color: #d5c9bd;
  }
}

.slider-arrow--left {
  left: -51px;
  background-image: url("./../images/left-arrow.png");
  border-right: none;
}

.slider-arrow--right {
  right: -51px;
  background-image: url("./../images/right-arrow.png");
  border-left: none;
}

@media (min-width: $tablet-width) {
  .slider-arrow {
    display: block;
  }
}

@media (min-width: $desktop-width) {
  .slider-arrow--left {
    left: -70px;
    border-right: 1px solid #ccc;
  }

  .slider-arrow--right {
    right: -70px;
    border-left: 1px solid #ccc;
  }

  .advantages__item.advantages__item--fast.slider__item.block {
    display: table-cell!important;
  }
}
